<template>
    <div class="house-detail-content">
        <p class="house-detail"></p>
        <p class="house-dec" :class="{shrink}">
            <span ref='houseDes' class="houseDes" :class="{conMore:!shrink}" v-show="shrink">{{houseDes}}</span>
           
            <span ref='houseDes2' class="houseDes houseDes2" v-show="!shrink">{{houseDes2}}</span>
             <span class="house-more" @click="houseDesMore()" v-if="isToolang" :class="{btnMore:shrink}">
                <i v-show="shrink">....</i>{{btn[shrink]}}
                <!-- v-if="isToolang" -->
            </span>
        </p>

    </div>
</template>
<script>
export default {
    name: 'houseDetail',
    data() {
        return {
            isToolang: false,
            houseDes: 'aaaaaaaaaaaaaaaaaaaaaaaaaaaaaaaaa1111111111111111111111111aaaaaaaaaaaaaaaaaaaaaaaaaaaaaaaaa1111111111111111111111111aaaaaaaaaaaaaaaaaaaaaaaaaaaaaaaaa1111111111111111111111111aaaaaaaaaaaaaaaaaaaaaaaaaaaaaaaaa1111111111111111111111111',
            houseDes2:"aaaaaaaaaaaaaaaaaaaaaaaaaaaaaaaaa1111111111111111111111111111111'aaaaaaaaaaaaaaaaaaaaaaaaaaaaaaaaa11111111111111111111111111111111",
            shrink: true,
            btn:{
                true:'',
                false:''
            },
        }
    },
    methods: {
        houseDesMore() {
            this.shrink = !this.shrink;
        }
    },
    mounted() {
        const houseDesEle = this.$refs.houseDes;   
        let rate = houseDesEle.offsetHeight / houseDesEle.offsetWidth;
        console.log(rate);
        
        
        if (rate > 0.34) {
            this.isToolang = true;
        }
    },
}
</script>

<style  scoped>
    .house-dec{
        position: relative;
    }
    .house-more{
        color:red;
        position: absolute;
        right: 0px;
        bottom: 0px;
        background: #ffffff;
    }
    .btnMore{
        color: blue;
    }

</style>


