<template lang="html">
  <div class="zhihu-res">
    <h3>{{msg}}</h3>
    <ul class="list" v-for="x in list.stories">
      <li>
        <img :src="x.images[0]" alt="">
        <p>{{x.title}}</p>
      </li>
    </ul>
  </div>

</template>

<script>
export default {
  data(){
    return{
      list:[],
      msg:"vue-resource",
      notes:"Vue     <p>{{x.title}}</p>"
    }
  },
  created(){
    // vue -resource 
    const loading = this.$loading({
          lock: true,
          text: 'Loading',
          spinner: 'el-icon-loading',
          background: 'rgba(0, 0, 0, 0.7)'
        });
        // setTimeout(() => {
        //   loading.close();
        // }, 2000);
    this.$http.get("https://zhihu-agent.herokuapp.com/get?api=/4/news/latest").then(function(data){
      this.list = data.data;
      console.log(this.list);
      loading.close();
    });
  },
  mounted(){

  },
  methods:{

  }
}
</script>

<style lang="css">
</style>



