<template lang="html">
    <div class="article">
      <top>{{title}}</top>
     
      <div class="main">
        <el-breadcrumb separator="/" style="padding-top: 20px;">
          <el-breadcrumb-item :to="{ path: '/mainx/article' }"></el-breadcrumb-item>
          <el-breadcrumb-item :to="{ path: '/mainx/article' }"></el-breadcrumb-item>
          <el-breadcrumb-item v-for="item in json" v-show="item.id == articid" :key="item.id">{{item.title}}</el-breadcrumb-item>
        </el-breadcrumb>
        <section class="content" v-for="item in json" v-show="item.id == articid" :key="item.id">
          <h3>{{item.title}}</h3>
          <p v-html="item.content"></p>
        </section>
      </div>

    </div>
</template>

<script>
import top from '../common/head'
export default {
  data(){
    return{
      title:"",
      articid:"",
      json:[
        {
          "id":1,
          "title":"vue",
          "content":"1.UIMint UI; <a>http://mint-ui.github.io/#!/zh-cn</a> <br /> githubhttps://github.com/ElemeFE/mint-ui <br/> 2. <b>vue-swiper</b> gitlab:https://github.com/ElemeFE/vue-swipe <br /> 3.  <b>vue-infinite-scroll</b> <br/> 4. <b>vue-loadmore</b> <br/> 5. toast <b>vue-toast-mobile</b> <br/> 6.indicator  <b>vue-indicator</b> <br/> 7. vue-msgbox <p> ----------------</p> <p>PC<b>element-ui</b> http://element-cn.eleme.io/#/zh-CN/component/dialog</p>"
        },
        {
          "id":2,
          "title":"vue",
          "content":"4. v-bind:class  status class.div v-bind:class={'class-a':'','class-b':''} vue   true or false class .class-aborder: 1px solid red .class-bborder: 1px solid blue; "
        },
        {
          "id":3,
          "title":"vuex ",
          "content":"<p>vuex?</p><p>https://vuex.vuejs.org/zh-cn/state.html; https://segmentfault.com/a/1190000009404727</p> "
        },
        {
          "id":4,
          "title":"vue",
          "content":"<p>Objec.definProperty()</p><p>get/set</p><p>Object.defineProperty()getsetgetset</p>"
        },
        {
          "id":5,
          "title":"",
          "content":`var quickSort = function(arr) {
                     <br/>
                    if (arr.length <= 1) { return arr; }
                       <br/>
                    var pivotIndex = Math.floor(arr.length / 2);
                        <br/>
                    var pivot = arr.splice(pivotIndex, 1)[0];
                        <br/>
                    var left = [];
                        <br/>
                    var right = [];
                        <br/>
                    for (var i = 0; i < arr.length; i++){
                          <br/>
                    if (arr[i] < pivot) {
                            <br/>
                    left.push(arr[i]);
                            <br/>
                    } else {
                            <br/>
                    right.push(arr[i]);
                            <br/>
                    }
                          <br/>
                    }
                        <br/>
                    return quickSort(left).concat([pivot], quickSort(right));
                        <br/>
                    };`
        }
        
      ]
    }
  },
  mounted(){
    // this.articid =location.hash.substr(-1);
    this.articid = this.$route.params.id;
    console.log(this.$route.params);
  },
  components:{
    top
  }
}
</script>

<style lang="css" scoped>
.main{
  max-width: 1200px;
  min-width: 1000px;
  margin: 20px auto;
  padding: 0px 20px 20px 20px;
  box-shadow:    0px -10px 0px 0px #fff,   /*  */
               -5px 0px 10px 0px rgba(0,0,0,0.5),   /*  */
               5px 0px 10px 0px rgba(0,0,0,0.5),    /*  */
               0px 10px 0px 0px #fff;    /*  */
}
.content{
  margin-top: 20px;
  border-top: 1px solid #dedede;
}
</style>



