<template lang="html">
  <div class="aboutme">
    <h3></h3>


    <span></span>
     <el-rate v-model="value1" class="rate" @change="rate($event)"></el-rate>
     

     <p>tab</p>
     <ul class="tab">
        <!-- <li id="interpreList" @click="change($event)">TAB1</li>
        <li id="chinapreList" @click="change($event)">TAB2</li> -->
        <li v-for="(item,index) in navlist" :class="{active:!(index-menuIndex)}"  @click='menuShow(index)'  v-text="item" >{{item}}</li>
      </ul>
      
    <audio src="/static/btn.mp3" controls="controls" id="myEmbed" hidden>
      Your browser does not support the audio element.
      </audio>
    <div class="tabConent">
      <div v-show='menuIndex==0'>
          <p></p>
        </div>
        <div v-show='menuIndex==1'>
          <p></p>
        </div>
        <div v-show='menuIndex==2'>
         <p></p>
        </div>
    </div>

    <button class="addclass" v-for="(value,index) in arr" :class="arr[value]" @click="addclass($event, index)"></button>
  


   <p>
     <button @click="dialog()"></button>
    </p>
    <v-dialog :dialog-msg="dialogMsg" v-show="dialogs"  @cancel="cancal" @confirm="confirm"></v-dialog>

  <div class="alertContsainer ">
    <p> <button @click="getAlert()" :disabled="disabled"></button> </p>
    <v-alert v-show="orAlert" :alertMsg="alertMsg" :num="num" @childByValue="getchildByValue"></v-alert>
  </div>
  <el-button type="primary" plain> <router-link to="/btnStatus"></router-link> </el-button>
 <!-- <img src="/static/img/timg.jpg" /> -->
  <el-button type="primary" @click="slotClick()">slot</el-button>
    <div class="slot">
      <slotChild v-show="slotShow" @closeSlot="closeSlot">
        <h3 slot="title">slot</h3> 
        <p slot="one">one(slot)</p>
    </slotChild>

    <v-slot>
      <h3>slot slot_2slot</h3>
    </v-slot>
    </div>

    <!-- <div class="swiper" @mousedown="down" @mouseup="up" @mousemove="move">
      <img src="../../../static/img/swiper.jpg"  @click="getImg" />
    </div>
    
    <div class="imgContainer" v-show="imgShow">
      <div class="img-conent" @click="imgHide">
        <img :src="indexImg" />
      </div>
    </div> -->
    <paging :total="total" :current-page='current' @pagechange="pagechange"></paging>
    <v-pagination :total="total" :current-page='current' @pagechange="pagechange"></v-pagination>
  </div>
  

</template>

<script>
import dialog from '../common/dialog'
import alert from '../common/alert'
import slotChild from '../common/slot'
import slotItem from '../common/slot_2'
import pagination from '../paging/page'

// var json1 = [{"name":"zzz"},{"name":"sss"}];
// var json2 = [{"name":"aaa"},{"name":"ddd"}];
export default {
  data(){
    return{
      orAlert: false,
      alertMsg:"",
       dialogMsg:"?",
       dialogs:false,
       value1: null,
       slotShow:false,
       msg:"",
       menuIndex:0,
       navlist: ['', '', ''],
       disabled:false,
       clientX:0,
       clientXNext:0,
       indexImg:"",
       imgShow:false,
       total: 50,     // 
       display: 10,   // 
       current: 1,   // ,
       num: 1,
       arr: ['active', 'active','active','active']
    }
  },
  created(){
    var storage = localStorage.getItem("start");
    this.value1 = parseInt(storage);
    // localStorage.clear();
  },
  mounted(){
  
  },
  components:{
    'v-dialog':dialog,
    'v-alert':alert,
    'slotChild':slotChild,
    'v-slot':slotItem,
    'v-pagination':pagination
  },
  mounted(){
   this.routerQuery = JSON.stringify(this.$route.query);
   console.log(this.$route.query);
  },
  methods:{
    move(event){
      // console.log(event);
    },
    getchildByValue(childNum) {
      this.num = childNum;
    },
    imgHide(){
      if(this.imgShow){
        this.imgShow =!this.imgShow;
      }
    },
    getImg(event){
      console.log(event.target.currentSrc);
      this.imgShow = true;
      this.indexImg = event.target.currentSrc;
    },
    // 
    down(event){
      console.log("X "+ event.clientX);
      this.clientX = event.clientX;

    },
    // 
    up(event){
      console.log("X "+ event.clientX);
      var D_value = event.clientX - this.clientX; // 
      // console.log(D_value);
    },

    menuShow(index){
      var that = this;
      that.menuIndex  = index;
      var btnMusic = document.querySelector("#myEmbed")
      btnMusic.play()
    },
    dialog(){
      var that = this;
      that.dialogs = true;
    },
    slotClick(){
      var that = this;
      that.slotShow = true;
    },
    addclass(enet, index){
      console.log(index);
      this.$set(this.arr, index, 'noActive')

    },
    closeSlot(){
      var that = this;
      that.slotShow = false;
    },
    cancal(msg){
      var that = this;
      that.dialogs = false;
      that.alertMsg = "msg";
      this.orAlert = true;
      setTimeout(function(){
        that.orAlert = false;
      },2000)
    },
    confirm(msg){
      var that = this;
      that.dialogs = false;
      that.alertMsg = "msg";
      this.orAlert = true;
      setTimeout(function(){
        that.orAlert = false;
      },2000)
    },
    pagechange:function(currentPage){
       console.log(currentPage);
       // ajax,  currentPage, 

     },
    getAlert(){
      var that = this;
      that.alertMsg = "toast";
      this.orAlert = true;
      that.disabled = true;
      clearTimeout(timer);
      let timer = setTimeout(function(){
        that.orAlert = false;
        that.disabled = false;
        
      },3000)
    },
    rate(e){
      var vue = this;
      e = parseInt(e);
      if(e<=2){
        vue.msg = "( )";
      }else if(2< e && e< 4){
          vue.msg = "  ";
      }else if(e >=4){
        vue.msg = " ////";
      }
      const h = this.$createElement;
        this.$message({
          message: h('p', null, [
            h('span', null, ' '),
            h('span', { style: 'color: rgb(247, 186, 42)' }, e +" ,"),
            h('span', null, vue.msg)
          ])
        });
      localStorage.setItem("start",e)
    }
  }
}
</script>

<style lang="css" scoped>
.swiper{
  width: 500px;
  height: 400px;
  background: yellowgreen;
}
.addclass {
  width: 100px;
  height: 20px;
  outline: none;
}
.active {
  background: red !important;
}
.noActive {
  background: #ccc
}
a{
  text-decoration: none;
  color: #000000;
}
.rate{
  display: inline-block;
}
.tab{
  list-style: none;
  overflow: hidden;
  padding: 0;
}
.tab li{
  float: left;
  width: 100px;
  text-align: center;
  height: 40px;
  line-height: 40px;
  border: 1px solid #cccccc;
  box-sizing: border-box;
  cursor: url('/static/img/hand.cur'),auto;
}
.active{
    color: #fff;
    background-color: #409EFF;
    border-color: #409EFF;
}
.imgContainer{
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: rgba(0, 0, 0, .8);
  z-index: 2;
}
.img-conent{
  position: absolute;
  top: 10%;
  left: 10%;
  right: 10%;
  bottom: 10%;
  z-index: 5;
}
 img{
  width: 100%;
}
</style>



