        <el-collapse-item title="3." name="3">
          <div>import view from '@/components/common/view'</div>
          <div>{
            path: '/view',
            name: '',
            component: view
          }</div>
          <div>children:[{
            path: '/view',
            name: '',
            component: view
          }]</div>
        </el-collapse-item>
        <el-collapse-item title="4. <router-view/>" name="4">
