                  component: douban,
                }
              ]
            }</div>
        </el-collapse-item>
        <el-collapse-item title="5.vue-resource" name="5">
          <div>cnpm install vue-resource --save</div>
          <div>main.js  1--import  VueResource  from 'vue-resource'  2-- Vue.use(VueResource) this $resource</div>
        </el-collapse-item>
        <el-collapse-item title="6." name="6">
          <div>1</div>
          <div></div>
        </el-collapse-item>
      </el-collapse>
    </div>
