    <el-table :data="tableData" height="450" border style="max-width: 1200px;
    min-width: 1000px;" class="table">
        <el-table-column prop="date"  label="" width="">
        </el-table-column>
        <el-table-column  prop="name" label=""  width="">
        </el-table-column>
      </el-table>
