</template>

<script>

export default {
  name: 'view',
  data () {
    return {
      activeNames: [''], //  name  
      msg: ' debug  Y/N  eslint,',
      tableData:[
        {
          date: "ElementUI.babelrc npm run dev  ",
          name: " .babelrcmain.js ElementUI  elementcss import ElementUI from 'element-ui'import 'element-ui/lib/theme-chalk/index.css'Vue.use(ElementUI)"
        },
        {
            date:"vue2.0js jscssstatic  img/js/css",
            name:"1-.js js export export function name(){js} 2-.vue   src/conponents/views static  ../../../static/js/..  import { time } from '../../../static/js/canvasTime.js' 3-.  time() 4-:  main.js import js import {time} form ../static/..  Vue.prototype.time = time;  vue  this.time() "
        },
        {
            date:"js ",
            name:""
        },
        {
            date:"",
            name:""
        }
      ]
    }
  },
  created(){},
  mounted(){

  },
  methods:{
    login(){

    },
    handleChange(){
      console.log(" change ");
    }
  }
}
</script>

