<style scoped>
.logo{
  display: inline-block;
  width: 100px;
  height: 100%;
  color:#409EFF ;
  font-size:50px ;
  font-weight: blod;
  text-align: center;
  line-height: 100px;
  position: fixed;
  top: 0px;
  left: 40px;
}

.container{
  text-align: center;
}
.table{
  margin: 20px auto;
}
.list{
  max-width: 1200px;
  min-width: 1000px;
  margin: 20px auto;
}
h1, h2 {
  font-weight: normal;
}
ul {
  list-style-type: none;
  padding: 0;
}

.msg{
  color: #878D99;
  font-size: 12px;
}
li {
  /*display: inline-block;*/
  margin: 10px 10px;
}
a {
  color: #42b983;
}
</style>



