<template lang="html">
  <div>
      <router-link to="/mainx/aboutme"><el-button type="primary" plain>  </el-button></router-link>
      <p style="text-align:center;">indexOfindex-1;</p>
    <div class="btnStatus" v-for="(item,index) in initData.list" >
            <div class="house-btn" >
                <button v-if="statusData[item.orderStatus].btnArr.indexOf('cancel')!== -1" ></button>
                <button v-if="statusData[item.orderStatus].btnArr.indexOf('cancelContract')!== -1"></button>
                <button v-if="statusData[item.orderStatus].btnArr.indexOf('goToSign')!== -1 " class="goSign"  ></button>
                <button v-if="statusData[item.orderStatus].btnArr.indexOf('goToCantract')!== -1" ></button>
                <button v-if="statusData[item.orderStatus].btnArr.indexOf('bill')!== -1 && item.cateId =='8'" class="goSign" @click="goBillURI(item)" v-touch-class:activeGoRed ></button>
            </div>
    </div>
    <h3 style="text-align:center;width:80%;margin:auto;"> indexOf </h3>
  </div>
</template>
<script>
    export default {
        data(){
            return{
                initData: {},
                statusData: {
                    0: {
                        title: '',
                        time: '',
                        class: {app: 'app'},
                        btnArr: ['cancel'],
                    },
                    1: {
                    title: '',
                    time: '',
                    class: {app: 'contractFail'},
                    btnArr: [],
                    },
                    2: {
                        title: '',
                        time: '',
                        class: {app: 'contractFail'},
                        btnArr: [],
                    },
                    3: {
                    title: '', //  
                    time: '',
                    class: {app: 'app'},
                    btnArr: ['goToSign', 'cancelContract'],
                    },
                    4: {
                    title: '', // 
                    time: '',
                    class: {app: 'contractColor'},
                    btnArr: ['goToCantract', 'bill'],
                    },
                    5: {
                    title: '', // 
                    time: '',
                    class: {app: 'contractFail'},
                    btnArr: [],
                    },
                },
            }
        },
        created:function(){
            this.initData = window.____json4fe;
            console.log(this.initData.list)

            if (Object.prototype.toString.call(this.initData)=='[object Array]' && this.initData.length==0) {
                alert('');
                return;
            }
        }
    }
</script>
<style lang="css" scoped>
    .btnStatus{
        width: 80%;
        height: 30px;
        line-height: 30px;
        background: rgb(42, 165, 108);
        margin: 20px auto;
    }
    a{
        text-decoration: none;
        display: block;
    }
</style>



